import React, { useState, useCallback} from 'react';
import { PieChart, Pie, Sector, LineChart, data, XAxis, YAxis, Tooltip, CartesianGrid, Line, ResponsiveContainer, Legend} from 'recharts';
import './App.css';
import UserIcon from './Assets/usericon.png';
import IconButton from '@mui/material/IconButton';
import {Home, ManageAccounts, Settings, ExitToApp} from '@mui/icons-material';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';

const croptype = [
    { name: "Tomatoes", value: 400 },
    { name: "Potatoes", value: 300 },
    { name: "Bell Pepper", value: 300 },
    { name: "Cucumber", value: 200 }
  ];

  const renderActiveShape = (props: any) => {
    const RADIAN = Math.PI / 180;
    const {
      cx,
      cy,
      midAngle,
      innerRadius,
      outerRadius,
      startAngle,
      endAngle,
      fill,
      payload,
      value
    } = props;
    const sin = Math.sin(-RADIAN * midAngle);
    const cos = Math.cos(-RADIAN * midAngle);
    const sx = cx + (outerRadius + 10) * cos;
    const sy = cy + (outerRadius + 10) * sin;
    const mx = cx + (outerRadius + 30) * cos;
    const my = cy + (outerRadius + 30) * sin;
    const ex = mx + (cos >= 0 ? 1 : -1) * 22;
    const ey = my;
    const textAnchor = cos >= 0 ? "start" : "end";
    return (
      <g>
        <text x={cx} y={cy} dy={8} textAnchor="middle" fill={fill}>
          {payload.name}
        </text>
        <Sector
          cx={cx}
          cy={cy}
          innerRadius={innerRadius}
          outerRadius={outerRadius}
          startAngle={startAngle}
          endAngle={endAngle}
          fill={fill}
        />
        <Sector
          cx={cx}
          cy={cy}
          startAngle={startAngle}
          endAngle={endAngle}
          innerRadius={outerRadius + 6}
          outerRadius={outerRadius + 10}
          fill={fill}
        />
        <path
          d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`}
          stroke={fill}
          fill="none"
        />
        <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none" />
        <text
          x={ex + (cos >= 0 ? 1 : -1) * 12}
          y={ey}
          textAnchor={textAnchor}
          fill="#333"
        >{`${value} tons`}</text>
        <text
          x={ex + (cos >= 0 ? 1 : -1) * 12}
          y={ey}
          dy={18}
          textAnchor={textAnchor}
          fill="#999"
        >
        </text>
      </g>
    );
  };

  const columns = [
    {
      id: 'crop',
      label: 'Crop',
      minWidth: 80,
      align: 'left',
      format: (value) => value.toLocaleString('en-US'),
    },
    {
      id: 'loc',
      label: 'Location',
      minWidth: 80,
      align: 'left',
      format: (value) => value.toLocaleString('en-US'),
    },
    {
        id: 'expqty',
        label: 'Exp Quantity',
        minWidth: 80,
        align: 'left',
        format: (value) => value.toFixed(2),
      },
      {
        id: 'ship',
        label: 'Expected Date of Shipping',
        minWidth: 80,
        align: 'right',
        format: (value) => value.toFixed(2),
      },
      {
        id: 'rot',
        label: 'Expected Date of Rotting',
        minWidth: 80,
        align: 'right',
        format: (value) => value.toFixed(2),
      },
  ];
  
  function createData(crop, loc, expqty, ship, rot) {
    return {crop, loc, expqty, ship, rot};
  }
  
  const rows = [
    createData('Tomatoes', '1-6', '100kg', '05/03/2022', '17/05/2022'),
    createData('Potatoes', '5-7', '800kg', '09/03/2022', '20/05/2022'),
    createData('Cucumber', '10-15', '900kg', '27/05/2022', '29/06/2022'),
    createData('Bell Pepper', '23-25', '200kg', '22/04/2022', '17/05/2022'),
    createData('Beef Steak', '8-10', '400kg', '15/04/2022', '23/05/2022'),
    createData('Lettuce', '16-18', '600kg', '10/03/2022', '25/05/2022'),
  ];
  const dbdata = [
    {
        param: '0',
        temp: 5,
        humidity: 14
    },
    {
        param: '10',
        temp: 15,
        humidity: 12
    },
    {
        param: '20',
        temp: 5,
        humidity: 10
    },
];
const dbdata2 = [
  {
      param: '0',
      ethylene: 5,
      H2S: 10
  },
  {
    param: '5',
    ethylene: 8,
    H2S: 12
},
  {
    param: '10',
    ethylene: 12,
    H2S: 12
  },
  {
    param: '15',
    ethylene: 13,
    H2S: 18
},
  {
      param: '20',
      ethylene: 18,
      H2S: 10
  },
];
  

function App() {
        const [activeIndex, setActiveIndex] = useState(0);
        const onPieEnter = useCallback(
        (_, index) => {
          setActiveIndex(index);
        },
        [setActiveIndex]
        );
        const [page, setPage] = React.useState(0);
        const [rowsPerPage, setRowsPerPage] = React.useState(10);

        const handleChangePage = (event, newPage) => {
            setPage(newPage);
        };

        const handleChangeRowsPerPage = (event) => {
            setRowsPerPage(+event.target.value);
            setPage(0);
        };

        return (
            <div className='Buyer-Dash2'>
                <div className='BuyerDash2-nav'>
                    <IconButton aria-label="Home">
                        <Home fontSize="large" sx={{ color: '#fff' }}/>
                    </IconButton>
                    <IconButton aria-label="ManageAccount">
                        <ManageAccounts fontSize="large" sx={{ color: '#fff' }}/>
                    </IconButton>
                    <IconButton aria-label="Settings">
                        <Settings fontSize="large" sx={{ color: '#fff' }}/>
                    </IconButton>
                    <IconButton aria-label="Exit">
                        <ExitToApp fontSize="large" sx={{ color: '#fff' }}/>
                    </IconButton>
                </div>
                <div className='BuyerDash2-header'>
                    <div className='BuyerUser2-container'>
                        <div className='BuyerDash2-title'>DashBoard</div>
                        <div className='BuyerDash2-user'>Warehouse01
                            <img src={UserIcon} className='Buyer2-icon' aria-label='Buyer-Icon'/>
                        </div>
                    </div>
                    <div className='BuyerStats2-container'>
                        <div className='BuyerPiechart2-container'>
                            Total Produce in the Warehouse
                            <h2>5,000 tons</h2> 
                        </div>
                        <div className='BuyerPiechart2-container'>
                            <h3>Warehouse01</h3>
                            <div className='Buyer2-row'>
                              Location -
                              <h3>&nbsp;Bangalore</h3>
                            </div>
                            <div className='Buyer2-row'>
                            Size of Warehouse -
                            <h3>&nbsp;50,000sq feet</h3>
                            </div>
                        </div> 
                        <div className='BuyerPiechart2-container'>
                            <PieChart width={400} height={400}>
                                <Pie
                                    activeIndex={activeIndex}
                                    activeShape={renderActiveShape}
                                    data={croptype}
                                    cx={200}
                                    cy={200}
                                    innerRadius={70}
                                    outerRadius={90}
                                    fill="#EE2288"
                                    dataKey="value"
                                    onMouseEnter={onPieEnter}
                                />
                            </PieChart>
                        </div>             
                    </div>
                    <div className='Buyer2-row'>
                    <div className="Graphs Graph-Container">
            <ResponsiveContainer height="90%" width="48%">
                <LineChart data={dbdata}>
                    <CartesianGrid stroke="#fff"/>
                    <XAxis dataKey="param" 
                        interval={'preserveStartEnd'} />
                    <YAxis dataKey="humidity"
                    interval={'preserveStartEnd'}/>
                    <Legend />
                    <Tooltip />
                    <Line type="monotone" dataKey="temp"
                        stroke="blue" activeDot={{ r: 8 }} />
                    <Line type="monotone" dataKey="humidity"
                        stroke="red" activeDot={{ r: 8 }} />
                </LineChart>
            </ResponsiveContainer>
            <ResponsiveContainer height="90%" width="48%">
                <LineChart data={dbdata2}>
                    <CartesianGrid stroke="#fff"/>
                    <XAxis dataKey="param" 
                        interval={'preserveStartEnd'} />
                    <YAxis dataKey="H2S"
                    interval={'preserveStartEnd'}/>
                    <Legend />
                    <Tooltip />
                    <Line type="monotone" dataKey="ethylene"
                        stroke="blue" activeDot={{ r: 8 }} />
                    <Line type="monotone" dataKey="H2S"
                        stroke="red" activeDot={{ r: 8 }} />
                </LineChart>
            </ResponsiveContainer>
            </div>

                    <Paper sx={{ width: '85vmin', overflow: 'hidden', margin: '1vmin', borderRadius: '2vmin'}}>
                        <TableContainer sx={{ maxHeight: 400 }}>
                            <Table stickyHeader aria-label="sticky table">
                        <TableHead>
                            <TableRow>
                                {columns.map((column) => (
                                    <TableCell
                                        key={column.id}
                                        align={column.align}
                                        style={{ minWidth: column.minWidth }}>
                                        {column.label}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows
                                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                .map((row) => {
                                    return (
                                        <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                            {columns.map((column) => {
                                            const value = row[column.id];
                                                return (
                                                    <TableCell key={column.id} align={column.align}>
                                                        <div style={{textDecoration: 'none', color: '#000'}}>
                                                        {column.format && typeof value === 'number'
                                                        ? column.format(value)
                                                        : value}
                                                        </div>
                                                    </TableCell>
                                                );
                                            })}
                                        </TableRow>
                                    );
                                })}
                        </TableBody>
                            </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[10, 25, 100]}
                        component="div"
                        count={rows.length}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage} />    
                    </Paper>
                    </div>
                </div>
            </div>
        )
    }    
    

export default App
